import { LocationQueryRaw } from "vue-router";

export const createFilters = (filters: Record<string, string | string[]>) => {
  if (!filters) {
    return "";
  }

  const searchParams = new URLSearchParams();
  const filtersWithValues = getFiltersWithValues(filters);

  for (const [name, value] of Object.entries(filtersWithValues)) {
    if (![null, undefined].includes(value)) {
      if (Array.isArray(value)) {
        for (const valueItem of value) {
          searchParams.append(`${name}[]`, valueItem);
        }
      } else {
        searchParams.append(name, value);
      }
    }
  }

  return searchParams.toString();
};

const getFiltersWithValues = (filters: any) => {
  return Object.keys(filters).reduce<Record<string, any>>((acc, key) => {
    const value = filters[key];
    if (value) {
      acc[key] = value;
    }
    return acc;
  }, {});
};

export const mapFiltersToQuery = (
  filters: Record<string, string | string[]>
) => {
  if (!filters) {
    return "";
  }

  const searchParams = new Map();
  const filtersWithValues = getFiltersWithValues(filters);

  for (const [name, value] of Object.entries(filtersWithValues)) {
    if (![null, undefined].includes(value)) {
      if (Array.isArray(value)) {
        for (const valueItem of value) {
          searchParams.set(`${name}[]`, valueItem);
        }
      } else {
        searchParams.set(name, value);
      }
    }
  }

  return searchParams.size ? Object.fromEntries(searchParams) : {};
};

export const mapFiltersFromQuery = (query: LocationQueryRaw) => {
  if (!query) {
    return {};
  }

  const filters = {};
  const queryWithValues = getFiltersWithValues(query);

  for (const [name, value] of Object.entries(queryWithValues)) {
    if (![null, undefined].includes(value)) {
      if (name.includes("[]")) {
        const key = name.replaceAll("[]", "");
        if (!filters[key]) {
          filters[key] = [];
        }
        filters[key].push(value);
      } else {
        filters[name] = value;
      }
    }
  }

  return filters;
};
