import {
  ApiRoutes,
  http,
  ApiResponse,
  ApiResponseData,
  createFilters,
} from "@/app/http";
import { getToken } from "@/api/crypto/token";
import { signAllBody } from "@/api/crypto/DeffiHellman";
import { ApiSingleResponse } from "@/app/http/types";

import {
  CreateEcommTerminalRequest,
  GetTerminalRequest,
} from "../model/ecomm.request";
import { EcommTerminal } from "../model/ecomm-terminal";
import { EcommTableColumns } from "../model/ecomm-table";
import { EcommTerminalRef } from "../model/ref/terminals";

export const createEcommTerminal = async (
  payload: CreateEcommTerminalRequest
) => {
  const payloadSignature = await signAllBody(payload, getToken());

  const { data } = await http.post(ApiRoutes.CREATE_ECOMM_TERMINAL, payload, {
    headers: {
      "X-Payload-Signature": payloadSignature,
    },
  });

  return data;
};

export const getEcommTerminalById = async (id: string) => {
  const { data: response } = await http.get<ApiSingleResponse<EcommTerminal>>(
    ApiRoutes.GET_ECOMM_TERMINAL,
    {
      params: { id },
    }
  );

  if (response.code !== 0) {
    return null;
  }

  return response.data;
};

export const editEcommTerminal = async (
  id: number,
  payload: CreateEcommTerminalRequest
) => {
  const payloadSignature = await signAllBody(payload, getToken());

  const { data } = await http.post(
    ApiRoutes.EDIT_ECOMM_TERMINAL,
    { ...payload, id },
    {
      headers: {
        "X-Payload-Signature": payloadSignature,
      },
    }
  );

  return data;
};

export const getEcommTerminals = async (
  filters?: GetTerminalRequest
): Promise<ApiResponseData> => {
  const filtersUrl = createFilters(filters);

  const { data: response } = await http.get<ApiResponse<EcommTerminal[]>>(
    ApiRoutes.GET_ECOMM_TERMINALS + "?" + filtersUrl
  );

  if (response.code !== 0) {
    return {
      list: [] as EcommTerminal[],
      totalCount: 0,
      page: 0,
      perPage: 0,
    };
  }

  return response.data;
};

export const getEcommTerminalRefs = async (): Promise<EcommTerminalRef> => {
  const { data: response } = await http.get<
    ApiSingleResponse<EcommTerminalRef>
  >(ApiRoutes.ECOMM_TERMINAL_REF);

  return response.data;
};

export const prepareEcommTerminalFoTable = (
  terminals: EcommTerminal[],
  refs: EcommTerminalRef
) => {
  if (!terminals?.length) {
    return [];
  }

  return terminals.map((terminal) => ({
    ...terminal,
    subRows: Object.keys(terminal.systems).map((systemName) => {
      const system = terminal.systems[systemName];
      const columns = Object.entries(EcommTableColumns);
      const acc: Record<string, any> = {};

      acc["Система"] = systemName;

      for (const [key, label] of columns) {
        if (key === "comis_type") {
          acc[label] = refs.comis_type?.find(
            (type) => type.id == system[key]
          )?.name;
          continue;
        }

        if (key === "request_type") {
          acc[label] = refs.request_type?.find(
            (type) => type.id == system[key]
          )?.name;
          continue;
        }

        if (key === "gateways") {
          acc[label] = refs.banks_list
            ?.filter((bank) => system[key]?.some((id) => id == bank.id))
            .map((bank) => bank.name)
            .join(", ");
          continue;
        }

        if (key === "balancer_type") {
          acc[label] =
            refs.balancer_type?.find((type) => type.id == system[key])?.name ||
            "Не вказано";
          continue;
        }

        if (key === "seq_gateway_requests") {
          acc[label] = system[key] ? "Так" : "Ні";
          continue;
        }

        if (key === "secret_key") {
          const secretKey = system[key];

          acc[label] = `secretCopy#${secretKey}`;
          continue;
        }

        acc[label] = system[key];
      }

      return acc;
    }),
  }));
};
