import { http } from "@/api/client";
import { ApiRoutes } from "@/api/routes";
import { ApiResponse, createFilters } from "@/app/http";
import { ApiSingleResponse } from "@/app/http/types";

import { EcommTransaction } from "../model/ecomm-transaction";
import { EcommTransactionsRef } from "../model/ref/transactions";

export const getEcommTransactions = async (filters: any = { system: 1 }) => {
  const filtersUrl = createFilters(filters);
  const { data: response } = await http.get<ApiResponse<EcommTransaction[]>>(
    `${ApiRoutes.GET_ECOMM_TRANSACTIONS}?${filtersUrl}`
  );

  if (response.code !== 0) {
    return {
      list: [] as EcommTransaction[],
      totalCount: 0,
      page: 0,
      perPage: 0,
    };
  }

  return response.data;
};

export const getRefEcommTransactions = async (
  filters: any = { system: 1 }
): Promise<EcommTransactionsRef> => {
  const filtersUrl = createFilters(filters);

  const { data: response } = await http.get<
    ApiSingleResponse<EcommTransactionsRef>
  >(`${ApiRoutes.GET_REF_ECOMM_TRANSACTIONS}?${filtersUrl}`);

  if (response.code !== 0) {
    return {
      filters: [] as EcommTransactionsRef["filters"],
      optionLists: {} as EcommTransactionsRef["optionLists"],
      fields: [] as EcommTransactionsRef["fields"],
    };
  }

  return response.data;
};
