export const mapEcommTerminalToRequest = (
  payload: CreateEcommTerminalRequest
) => {
  const normalizedPayload: any = {
    ...payload,
    systems: Object.keys(payload.systems).reduce((acc, key) => {
      const system = payload.systems[key];
      if (Object.keys(system).length > 0) {
        acc[key] = system;
      }

      return acc;
    }, {}),
  };

  return proccessObject(normalizedPayload, {
    "systems.Ecomm.comis_rules": (value) => JSON.stringify(value),
    "systems.A2C.comis_rules": (value) => JSON.stringify(value),
    "systems.C2A.comis_rules": (value) => JSON.stringify(value),

    "systems.Ecomm.balancer_rules": (value) => JSON.stringify(value),
    "systems.A2C.balancer_rules": (value) => JSON.stringify(value),
    "systems.C2A.balancer_rules": (value) => JSON.stringify(value),

    "systems.Ecomm.gateways": (value) => value?.map((v) => parseInt(v)),
    "systems.A2C.gateways": (value) => value?.map((v) => parseInt(v)),
    "systems.C2A.gateways": (value) => value?.map((v) => parseInt(v)),

    "systems.Ecomm.balancer_type": (value) => parseInt(value),
    "systems.A2C.balancer_type": (value) => parseInt(value),
    "systems.C2A.balancer_type": (value) => parseInt(value),

    "systems.Ecomm.comis_type": (value) => parseInt(value),
    "systems.A2C.comis_type": (value) => parseInt(value),
    "systems.C2A.comis_type": (value) => parseInt(value),
  });
};

