export const mapTransactionsFromRequest = (
  transactions: EcommTransaction[],
  fields: EcommTransactionField[]
): EcommTransactionForTable[] => {
  return transactions.map((transaction) => {
    return fields.reduce((acc, field, key) => {
      if (field.type === "coloredText") {
        // Поле с статусом, где первый элемент массива - название статуса, второй - цвет
        const value = transaction[key];

        if (value) {
          acc[field.name] = value[0] || "";

          // status_color - используется в таблице для уставноки цвета ячейки
          acc["status_color"] = value[1];
        }

        return acc;
      }

      if (field.additional) {
        const subRow = acc["subRows"] ? acc["subRows"][0] : {};
        acc["subRows"] = [{ ...subRow, [field.name]: transaction[key] }];

        return acc;
      }

      acc[field.name] = transaction[key] || "";

      return acc;
    }, {});
  });
};

export const mapTerminalsFilterToRequest = (filters: any): any => {
  if (!filters) {
    return { page: "0", perPage: "10" };
  }

  return Object.keys(filters).reduce((acc, key) => {
    const value = filters[key];
    if (["system", "gateway"].includes(key) && !Array.isArray(value)) {
      acc[key] = [value];
      return acc;
    }

    acc[key] = value;
    return acc;
  }, {});
};
