import { defineStore } from "pinia";
import { reactive, ref } from "vue";

import { getEcommTerminalRefs as getEcommTerminalRefsAPI } from "../api/ecomm.api";
import { getRefEcommTransactions } from "../api/ecomm-transactions.api";

import { EcommTerminalRef } from "../model/ref/terminals";
import { EcommTransactionsRef } from "../model/ref/transactions";

export type EcomTerminalState = {
  isFetching: boolean;
  refs: EcommTerminalRef | null;
  transactionsRefs: EcommTransactionsRef | null;
};

export const useEcommRefsStore = defineStore("ecomm-refs", () => {
  const refs = ref<EcommTerminalRef | null>(null);
  const transactionsRefs = ref<EcommTransactionsRef | null>(null);
  const isFetching = ref(false);

  async function getEcommTerminalRefs() {
    try {
      isFetching.value = true;
      refs.value = await getEcommTerminalRefsAPI();
    } catch (e) {
      console.error(e);
    } finally {
      isFetching.value = false;
    }
  }
  async function getEcommTransactionsRefs(system = "1") {
    try {
      isFetching.value = true;
      transactionsRefs.value = await getRefEcommTransactions({ system });
    } catch (e) {
      console.error(e);
    } finally {
      isFetching.value = false;
    }
  }

  return {
    refs,
    transactionsRefs,
    isFetching,
    getEcommTerminalRefs,
    getEcommTransactionsRefs,
  };
});
