import get from "lodash.get";
import set from "lodash.set";

const isEmptyValue = (value) => {
  return (
    value === null ||
    value === undefined ||
    value === "" ||
    (Array.isArray(value) && value.every(isEmptyValue)) ||
    (typeof value === "object" &&
      value !== null &&
      Object.values(value).every(isEmptyValue))
  );
};

export function cleanupObject(obj, { ignoredFields = [] } = {}) {
  if (typeof obj !== "object" || obj === null) {
    return obj;
  }

  const cleanedObj = Array.isArray(obj) ? [] : {};

  for (const key in obj) {
    if (ignoredFields.includes(key)) {
      cleanedObj[key] = obj[key];
      continue;
    }

    const value = obj[key];

    if (typeof value === "object" && value !== null) {
      const cleanedValue = cleanupObject(value);
      if (!isEmptyValue(cleanedValue)) {
        cleanedObj[key] = cleanedValue;
      }
    } else if (!isEmptyValue(value)) {
      cleanedObj[key] = value;
    }
  }

  return cleanedObj;
}

export const proccessObject = (
  obj,
  rules: Record<string, (value: any) => any>
) => {
  const newObj = { ...obj };

  for (const key in rules) {
    const rule = rules[key];
    const value = get(obj, key);

    if (rule && value) {
      set(newObj, key, rule(value));
    }
  }

  return newObj;
};
